
.little-header {
    padding: 12px 0;
    width: 100%;
    text-align: center;
}
.little-header-img {
    width: 161px;
    height: 19.000000000000004px;
}
.item {
    height: 260px;
    position: relative;
    width: 170.99999999999997px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    /*  */
    flex-direction: column;
    align-content: flex-start;
    flex-shrink: 0;
    background: #fff;
    margin-top: 9px;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 4.5px;
    margin-right: 4.5px;
    float:left;
}
.item-img {
    width: 170.99999999999997px;
    height: 165.99999999999997px;
}
.item-title {
    margin-top: 11.499999999999998px;
    height: 37px;
    position: relative;
    overflow: hidden;
    line-height: 18.5px;
    text-overflow: ellipsis;
    min-height: 0;
}
.item-title-p {
    font-size: 13.000000000000004px;
    color: #333333;
}
.item-price-box {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    flex-direction: row;
    margin-top: 11px;
    height: 20px;
    line-height: 20px;
}
.item-sign {
    font-size: 11px;
    margin-left: getUnit(-6);
    margin-bottom: getUnit(-1);
}
.item-price {
    font-size: 14px;
    line-height: 16.000000000000004px;
    color: #ff5500;
}
.item-payed {
    font-size: 11px;
    color: #999999;
    margin-top: 2.0000000000000004px;
}
.item-h {
    color: #ff5500;
}
.iconfont{
    font-size: 24px;
}
.flex{
    position: relative;
    float:left;
    padding-left:10px;
}